.accordion-pointer {
  display: none;

  @include vp-767 {
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    margin: 0;
    padding: 0;
    border: 0;
    width: 40px;
    height: 40px;
    background-color: transparent;

    &::before,
    &::after {
      content: "";
      position: absolute;
      background-color: $color-default-white;
    }
  }
}
